/**
 * input-box 
 *
 * @props theme - 
 * @props value - 
 * @props placeholder - 
 * @props readOnly - 
 * @props queryName - 
 * @props autoCompletion - 
 * @props completionUrl - url
 * @props completionItems - 
 * @props keyQuery - 
 * @props processCompletion - 
 * @props empty - 
 * @props verifedType - 
 * @props regex - 
 * @props type - ( text | textarea )
 * @props row - textarea 
 * @props hidden - 
 * @props errorMessage - input 
 * @props maxLength - input
 * @props errorTip -  bubble | tip 
 * @props number -  nmuber 
 * @props formatMessage - 
 * @props addon - ex: /
 *
 * @events change - inputBox
 * @events blur - inputBoxblur
 * @events focus - inputBoxfocus
 * @events keyup - inputBoxkeyup
 * @events inputBoxEvent.completion.click - 
 * @events inputBoxEvent.completion.change - 
 */

const Vue = require('vue');

require('./input-box.scss');
const template = require('./input-box.tpl');

const { ajaxRtn } = require('src/common/utils/utils');
const { initVerfication } = require('./validate.js');
const { inputBox: inputBoxEvent } = require('components/config/event.json');
const { inputBox: inputBoxHub } = require('components/config/componentHub.json');
const dataUtil = require('src/common/utils/data');
const baseMixin = require('components/mixin/base');
const formMixin = require('components/mixin/form');

const tip = require('components/base/pop/tip');

const keyUpInterval = 500;
const TYPE_TEXT_AREA = 'textarea';
const TYPE_TEXT = 'text';
const ERROR_MESSAGE_TIP = 'tip';
const ERROR_MESSAGE_BUBBLE = 'bubble';

const InputBox = {
  name: "InputBox",

  template,

  mixins: [baseMixin, formMixin],

  props: {
    theme: {
      type: String,
      default: "primary"
    },

    value: {
      type: [String, Number],
      default: ""
    },

    placeholder: {
      type: String,
      default: ""
    },

    readOnly: {
      type: Boolean,
      default: false
    },

    queryName: {
      type: String,
      default: ""
    },

    completionItems: {
      type: Array,
      default: () => []
    },

    completionUrl: {
      type: String,
      default: ""
    },

    autoCompletion: {
      type: Boolean,
      default: false
    },

    keyQuery: {
      type: String,
      require: true
    },

    empty: {
      type: Boolean,
      default: true
    },

    maxLength: Number,

    hidden: {
      type: Boolean,
      default: false
    },

    type: {
      type: String,
      default: "text"
    },

    row: {
      type: Number,
      default: 4
    },

    verifedType: String,

    regex: String,

    processCompletion: Function,

    errorMessage: {
      type: String,
      default: ""
    },

    errorTip: {
      type: String,
      default: "tip"
    },

    number: {
      type: Boolean,
      default: false
    },

    formatMessage: String,

    addon: String

  },

  data() {
    return {
      themeClass: this.theme ? `theme-${this.theme}` : '',
      focusInput: false,
      completionDisplay: false,
      keyuped: false,
      dangerTip: "",
      dataTypeName: "",
      verified: true,
      currentCompletionIndex: 'undefined',
      bubbleDisplay: false
    }
  },

  methods: {
    _init() {
      this._initVerfication();
      this._initVerfiedMessage();
    },

    /**
     * 
     * @return {Object} this - 
     */
    _initVerfication() {
      if (this.regex) {
        this.regex = new RegExp(this.regex);

        return this;
      }

      var verify = initVerfication(this.verifedType);

      if (verify) {
        this.regex = verify.regex;
        this.dataTypeName = verify.dataTypeName;
      }

      return this;
    },

    /**
     * 
     * @return {Object} this - 
     */
    _initVerfiedMessage() {
      this.emptyMessage = this.errorMessage ? this.errorMessage : '';
      this.lengthMessage = this.errorMessage ? this.errorMessage : '';

      if(!this.formatMessage){
        this.formatMessage = this.errorMessage ? this.errorMessage + '' : this.dataTypeName + '';
      }

      return this;
    },

    /**
     * 
     * @return {Object} this - 
     */
    _processCompletion() {
      this.processCompletion && this.processCompletion.call(null, this.completionItems);

      return this;
    },

    /**
     * 
     * @return {Object} this - 
     */
    _fetchCompletion(key) {
      if (!this.autoCompletion) {
        this.completionDisplay = this.completionItems.length === 0 ? false : true;

        return false;
      }

      $.ajax({
        type: 'get',
        url: this.completionUrl,
        data: {
          [this.keyQuery]: key
        },
        success: (result) => {
          if (result.code === 0) {
            this.completionItems = result.data;
            this._processCompletion();
          } else {
            this.completionItems = [];
          }

          this.completionDisplay = this.completionItems.length === 0 ? false : true;

          return this;
        }
      });

      return this;
    },

    /**
     *  value  change 
     * @return {Object} this - 
     */
    _dispatchChange() {
      this.$dispatch(inputBoxEvent.change, {
        dispatcher: this,
        value: this.value,
        queryName: this.queryName
      });

      return this;
    },

    /**
     * 
     *
     * @param {Boolean} - 
     * @return {Object} - this - 
     */
    verify(firstVerify) {
      if (!this.value && this.value !== 0) {
        if(!this._verifyEmpty()) {
          this.verified = false;

          // TODO bug
          $(window).scrollTop($(this.$el).scrollTop())

          return false;
        }

        this.verified = true;
        this.dangerTip = '';

        return this;
      } else {
        if (this.number && isNaN(this.value)) {
          this.dangerTip = `${this.errorMessage}`;
          this.verified = false;

          return false;
        }

        if(this.maxLength){
          if (this.value.toString().length > this.maxLength) {
            this.dangerTip = this.number ?
                `${this.lengthMessage}${this.maxLength}!` :
                `${this.lengthMessage}${this.maxLength}!`;

            this.verified = false;

            return false;
          }
        }

        if (this.regex || this.verifedType) {
          if (!this.regex.test(this.value)) {
            this.dangerTip = firstVerify ? '' : this.formatMessage;
            this.verified = false;

            return false;
          }
        }

        this.verified = true;
        this.dangerTip = '';
        return this;
      }
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    _verifyEmpty() {
      if(!this.empty || this.verifedType) {
        if (this.bubbleDisplay) {
          this.dangerTip = firstVerify ? '' : ` ${this.emptyMessage}!`;
        } else {
          this.dangerTip = ` ${this.emptyMessage}!`;
        }
        this.verified = false;
        return false;
      }

      return true;
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    validate() {
      this.verify();

      if (!this.verified) {
        tip(this.dangerTip);

        return false;
      }

      return this;
    },

    /**
     * 
     *
     * @return {Object}
     */
    _clickCompletion(item, index) {
      this.value = item.text;
      this.currentCompletionIndex = index;

      this.fold().$dispatch(inputBoxEvent.completion.click, {
        value: item.value,
        text: item.text,
        dispatcher: this
      });

      this.$dispatch(inputBoxEvent.completion.change, {
        value: this.completionItems[index].value,
        text: this.completionItems[index].text,
        queryName: this.queryName,
        dispatcher: this
      });

      return this;
    },

    /**
     * 
     *
     * @return {Object}
     */
    _processCompletion() {
      if (!this.edit) {
        return this;
      }

      this.processCompletion && this.processCompletion.call(null, this.completionItems);

      return this;
    },

    /**
     * text  value
     * @param {Number} - 
     */
    getCompletionItem(index = this.currentCompletionIndex) {
      if (this.currentCompletionIndex === 'undefined') {
        return 'undefined';
      }

      return this.completionItems[index];
    },

    /**
     *  focus 
     * @return {Object} this - 
     */
    focus() {
      this.verified = true;

      this.$dispatch(inputBoxEvent.focus, {
        dispatcher: this
      });
    },

    /**
     *  blur 
     * @return {Object} this - 
     */
    blur() {
      this.$dispatch(inputBoxEvent.blur, {
        dispatcher: this
      });
      this.focusInput = false;
    },

    /**
     *  keyup 
     * @return {Object}
     */
    keyup() {
      this.$dispatch(inputBoxEvent.keyup, {
        dispatcher: this
      });

      if (this.keyuped) {
        return false;
      }

      this.keyuped = true;
      setTimeout(() => {
        this.keyuped = false;
      }, keyUpInterval);

      return this._fetchCompletion(this.value);
    },

    /**
     * 
     *
     * @return {Object}
     */
    fold() {
      this.completionDisplay = false;

      return this;
    },

    /**
     * 
     *
     * @return {Object}
     */
    spread() {
      this.completionDisplay = true;

      return this;
    }
  },

  watch: {
    'value'(val, oldVal) {
      if (dataUtil.dataType(val) !== 'number'
          && this.nubmer && val !== '' && val && !isNaN(val)) {
        this.value = Number(val);
      }

      //  
      if ((this.autoCompletion && val !== '')
          || Object.is(val, oldVal) || val === oldVal) {

        return false;
      } else {
        this._dispatchChange()
        this.bubbleDisplay && this.verify();
      }

    }
  },

  computed: {
    dangerTipDisplay() {
      return !!this.dangerTip && this.bubbleDisplay;
    },
    isTextarea() {
      return this.type === TYPE_TEXT_AREA
    },
    isText() {
      return this.type === TYPE_TEXT
    },
    errorBorderDisplay() {
      return !this.verified;
    }
  },

  created() {
    this.bubbleDisplay = this.errorTip === ERROR_MESSAGE_TIP ? false : true;
  },

  ready() {
    COMMON.componentHub[inputBoxHub].push(this);
  }
}

module.exports = Vue.component('input-box', InputBox);



