/**
 * bubble 
 *
 *  bubble bubble
 * bubble props 
 *
 * @props theme - 
 * @props poptype - 
 * @props width - bubble
 * @props headername - 
 * @props message - alert
 * @props bubbleDisplay - bubble
 * @props relative -  bubble
 * @props hideRightNow -  bubble bubble 
 *
 * @slot body - confirm
 *
 */
const Vue = require('vue');

const baseMixin = require('components/mixin/base');
const { bubble: bubbleHub } = require('components/config/componentHub.json');

require('components/base/icon/icon')

require('./bubble.scss');
var template = require('./bubble.tpl');

const ARROW_HEIGHT = 20;

var Bubble = Vue.extend({
  name: "Bubble",

  template,

  mixins: [baseMixin],

  props: {
    theme: {
      type: String,
      default: "primary"
    },
    message: {
      type: String,
      default: ""
    },

    bubbleDisplay: {
      type: Boolean,
      default: false
    },

    relative: {
      type: Boolean,
      default: false
    },

    hideRightNow: {
      type: Boolean,
      default: false
    },

    width: {
      type: Number,
      default: 0
    }
  },

  data: () => {
    return {
      popDisplay: false,
      mouseOnBubble: false,
      bubbleDisplayCounter: {},
      displayInterval: 800
    }
  },

  methods: {
    _init() {
      if (this.hideRightNow) {
        this.displayInterval = 0;
      }
    },
    /**
     * bubble
     * @return {Object} - 
     */
    _initPosition(target) {
      var $target = $(target);
      var position = this.relative ? $target.position() : $target.offset();

      var width = $target.outerWidth();
      var height = $target.outerHeight();

      var $el = $(this.$el);
      var bubbleWidth = $el.outerWidth();
      var bubbleHeight = $el.outerHeight();

      $el.css({
        top: position.top + height + ARROW_HEIGHT/2,
        left: position.left - bubbleWidth / 2 + width / 2
      });

      return this;
    },

    /**
     * bubble
     * @return {Functio} - bubble
     */
    show(target) {
      clearTimeout(this.bubbleDisplayCounter);
      this.bubbleDisplay = true;

      this.$nextTick(() => {
        this._initPosition(target);
      });

      return this;
    },

    /**
     * bubble
     * @return {Object} - 
     */
    hide() {
      clearTimeout(this.bubbleDisplayCounter);
      this.setTimeoutBubbleDisplay();

      return this;
    },

    /**
     * bubble
     * @return {Object, String}
     **/
    info(text) {
      if (text) {
        this.message = text;
        return this;
      }
      return this.message;
    },

    /**
     * bubble
     **/
    mouseOver() {
      this.mouseOnBubble = true;
      clearTimeout(this.bubbleDisplayCounter);
    },

    /**
     * bubble
     **/
    mouseLeave() {
      this.mouseOnBubble = false;
      this.setTimeoutBubbleDisplay();
    },

    /**
     * bubble
     **/
    setTimeoutBubbleDisplay() {
      this.bubbleDisplayCounter = setTimeout(() => {
        this.bubbleDisplay = false;
      }, this.displayInterval);
    }
  },

  ready() {
    COMMON.componentHub[bubbleHub].push(this);
  }
});

module.exports = Vue.component('bubble', Bubble);


