/*
* : select
* @params:
* url 
* optionData option [{key:'',text:''}]
* selectedVal 
* initSelectedName 
* throwSelectedName 
* optionGroup 
* optionGroupKey key
* ajax 
* ajaxKey key
* mutiple 
* searchShow 
* searchKey key
* localSearch 
* grayValue 
*/
const Vue = require('vue');
const ajax = require('appUtil/ajax');
require('./vm-select.scss');
const template = require('./vm-select.tpl');

const VSelect = {
	name: "VSelect",
	template,
	data(){
		return {
			optionArr: [],//
			search: '',//model
			tmpOptionArr:[],//
			tmpOptionNameArr:[],//
			selectedName:'',//
			backSearch: null//search
		}
	},
	props: {
		url:{//
			type: String
		},
		optionData: {//option
			type: Array,
			twoWay: true,
			default(){
				return [{
					key: '123',
					text: ''
				}]
			}
		},
		selectedVal: {//
			twoWay: true,
			type: [String, Number]
		},
		initSelectedName:{//select
			twoWay: true,
			type: [String, Number]
		},
		throwSelectedName: {//
			twoWay: true,
			type: [String, Number]
		},
		throwAjaxData: {
			twoWay: true,
			type: [Array],
			default() {
				return [];
			}
		},
		optionGroup: {//
			type: Boolean,
			default: false
		},
		ajax: {//
			type: Boolean,
			default: false
		},
		ajaxKey: {//key
			type: Object
		},
		optionGroupKey: {//option key
			type: Object
		},
		mutiple:{//
			type: Boolean,
			default: false
		},
		searchShow: {//
			type: Boolean,
			default: true
		},
		searchKey: {//
			type: String
		},
		localSearch: {//
			type: Boolean,
			default: false
		},
		grayValue: {
			type: [String, Number],
			default: ''
		}
	},
	ready() {
		window.SELECT = [];
		this.init();
		$(window).on('click', (e)=> {
			let parents = $(e.target).parents('.vm-select');
			if (parents.length === 0) {
				$('.select-box').removeClass('open');
			}
		});
	},

	methods: {
		init() {
			if (!this.url) {
				this.$set('optionArr', this.optionData)
			} else {
				this.fetchData();
			}

			//
			this.backSearch = !this.url ? this.optionData : this.optionArr;

			if (this.initSelectedName) {
				this.selectedName = '' + this.initSelectedName;
				this.selectedVal = '' + this.selectedVal;
				this.tmpOptionArr = this.selectedVal.split(',');
				this.tmpOptionNameArr = this.selectedName.split(',');
			}
		},

		fetchData(params) {
			let url = this.url;

			if (!url) {
				return false;
			}

			params = params ? params : {};
			ajax('get', url).query(params).end((err, res) => {
        if (res.body.code === 0) {
          this.$set('optionArr', res.body.data);
          this.throwAjaxData = res.body.data;
        } else {

        }
      });
		},

		searchLocal(val) {
			let arr = this.optionData.filter((o)=>{
				return (o.text&&o.text.indexOf(val) > -1) && this.findEleIndex(this.tmpOptionArr, o.key);
			});

			//
			if (val.toString().length == 0) {
				arr = this.optionData;
			}

			this.backSearch = arr;
		},

		isOpen(e) {
			if (!e) {
				return false;
			}

			let dom = $(e.target).parents('.select-box');
			

			$('.select-box').not(dom).removeClass('open');

			dom.toggleClass('open');
			let children= dom.find('.dropdown-menu').children('.v-select');
			
			if (children.children('div').length > 6) {
				 children.css('overflow-y', 'scroll');
			}
		},

		findEleIndex(val, arr) {
			let index = -1;

			for (let i = 0; i < arr.length; i++) {
				if (val == arr[i]) {
					index = i;
					break;
				}
			}

			return index;
		},

		initSelect(val) {
			let str =  '' + this.selectedVal;
			let arr = str.split(',');
			let boolRes = false;

			for (let i = 0; i < arr.length; i++) {
				if (val == arr[i]) {
					boolRes =  true;
					break;
				}
			}

			return boolRes;
		},

		addVal(e, val, name) {
			let index = this.findEleIndex(val, this.tmpOptionArr);
			let $tmpEle = $(e.target);
			let $span = $tmpEle.find('.check-mark');
			$span = $span.length == 0 ? $tmpEle.siblings('.check-mark') : $span;
			let $p = $span.parents('p');

			if (index === -1) {
				//
				let dom = $p.addClass('selected');

				//
				if (this.mutiple) {
					this.tmpOptionArr.push(val);
					this.tmpOptionNameArr.push(name);
				} else {
					$p.parents('div').siblings('div').children('p').removeClass('selected');

					this.tmpOptionArr[0] = val;
					this.tmpOptionNameArr[0] = name
				}

			} else {
				$p.removeClass('selected');

				if (!this.mutiple) {
					$p.siblings('p')
					.removeClass('selected');
				}

				this.tmpOptionArr.splice(index, 1);
				this.tmpOptionNameArr.splice(index, 1);
			}

			//select name
			this.selectedVal = this.tmpOptionArr.join(',');
			this.selectedName = this.tmpOptionNameArr.join(',');

			//
			this.isOpen(e);

		},

		clearVal(e) {
			this.throwSelectedName = '';
			this.initSelectedName = '';
			this.selectedVal = '';
			this.selectedName = '';
			this.isOpen(e);
		}
	},

	watch:{
		search(val, oldVal) {
			if (!this.localSearch) {
				let params = `{"${ this.searchKey }": "${val}"}`;
				params = JSON.parse(params);
				this.fetchData(params);
			} else {
				this.searchLocal(val);
			}
		},

		selectedVal(val, oldVal) {
			//
			if (val.length == 0) {
				this.selectedName = '';
			}
		},

		selectedName(val, oldVal) {
			//
			if (val) {
				this.throwSelectedName = val;
			}
		},

		optionData: function(val, oldVal) {
			if (val) {
				this.backSearch = !this.url ? this.optionData : this.optionArr;
			}
		}
	}
}

module.exports = Vue.component('vm-select', VSelect);


