    /**
     * [validateEditStall description] 
     * @return {[type]} [description]
     */
    validateEditStall() {
      let stall = this.oneStallArr;

      if (stall.stallNumber.toString().length === 0) {
        return {
          "res": false,
          "msg": ""
        };
      }

      if (!stall.boxId || stall.boxId.toString().length === 0) {
        return {
          "res": false,
          "msg": "ID"
        };
      }

      // if (stall.lockId.toString().length === 0) {
      //   return {
      //     "res": false,
      //     "msg": ""
      //   };
      // }

      // if (stall.magnetismId.toString().length === 0) {
      //   return {
      //     "res": false,
      //     "msg": ""
      //   };
      // }

      if ((!stall.lockId || stall.lockId.toString().length === 0) && (!stall.gunCode || stall.gunCode.toString().length === 0) && (stall.magnetismId || stall.magnetismId.toString().length === 0)) {
        return {
          "res": false,
          "msg": "//ID"
        };
      }

      // if (!stall.gunId || stall.gunId.toString().length === 0) {
      //   return {
      //     "res": false,
      //     "msg": ""
      //   };
      // }

      // if (!stall.gunCode || stall.gunCode.toString().length === 0) {
      //   return {
      //     "res": false,
      //     "msg": "ID"
      //   };
      // }

      //
      if (stall.gunId && stall.gunId.toString().length > 0 && (!stall.gunType || stall.gunType.toString().length === 0)) {
        return {
          "res": false,
          "msg": ""
        };
      }

      if (stall.gunId && stall.gunId.toString().length > 0 && stall.chargerWay == 1 && (!stall.costId || stall.costId.toString().length === 0)) {
        return {
          "res": false,
          "msg": ""
        };
      }

      return {
        "res": true,
        "msg": ""
      };

    },

