/**
 * pop 
 *
 * @props theme - 
 * @props items - tab
 * @props currentIndex - tab
 *
 */

const Vue = require('vue');

const baseMixin = require('components/mixin/base');
const { tab: tabEvent } = require('components/config/event.json');

const template = require('./tab.tpl');
require('./tab.scss');

const Tab = {
  name: "Tab",

  mixins: [baseMixin],

  template,

  props: {
    theme: {
      type: String,
      default: "primary"
    },

    items: {
      type: Array,
      required: true
    },

    currentIndex: {
      type: Number,
      default: 0
    }
  },

  data: function () {
    return {
      currentItem: this.items[0],
      nextItem: this.items[1],
      nextIndex: 1,
      itemCount: this.items.length,
      themeClass: this.theme ? `theme-${this.theme}` : ''
    }
  },

  methods: {
    _init() {
      if (this.itemCount === 1) {
        this.nextItem = this.items[0]
        this.nextIndex = 0;
      } else {
        this.nextItem = this.items[1];
        this.nextIndex = 1;
      }
    },

    /**
     * tab
     * @param { Number } - tab
     * @return { Object }
     */
    select(index) {
      this.currentIndex = index;
      this.$dispatch(tabEvent.change, {
        dispatcher: this,
        item: this.items[index],
        index: index
      });

      return this;
    },

    /**
     * 
     * @param { Number } - tabItems  index
     * @return { Boolean }
     */
    isActive(index) {
      return index === this.currentIndex;
    }
  }
}

module.exports = Vue.component('tab', Tab);


