const template = require('./car-details.tpl');
require('./car-details.scss');

const store = require('appVuex/store');
require('appUtil/ajaxSetting');
const {
  apiPath
} = require('appService/api');
const tip = require('components/base/pop/tip');
const {
  setBreadCrumb
} = require('src/common/vuex/action/common');
require('components/base/pop/pop');
require('components/base/input-box/input-box');
const ajaxHttp = require('appUtil/ajaxHttp');

const CarDetails = {
  template,
  store,
  vuex: {
    actions: {
      setBreadCrumb,
    }
  },
  data() {

    return {
      gundatas: null,
      datas: {
        chargerDataVo: {},
        boxVo: {},
        lockVo: {},
        sensorVos: {}
      },
      handle: true,
      orderBol: false,
      gunBol: false,
      inductorBol: false,
      lockBol: false,
      controlBol: false,
      acBol: false,
      warnIndex: null,
      statusBol: false,
      baseUrl: apiPath.site.carDetails,
      carCode: '',
      thirdpartyOrderId: null,
      stallCode: '',
      siteId: null,
      chargringBol: false,
      noteItem: {},
      noticeList: [{
        name: ":Edward yang | edward@chargerlink.com",
        a: " -- ",
        b: " -- ",
        c: " -- "
      }],
      gunstatus:null,
      introduceBol:false
    }
  },
  route: {
    data: function(transition) {
      document.title = ''
      transition.next()
    }
  },
  ready() {
    this.carCode = this.$route.query.carCode;
    this.stallCode = this.$route.query.stallCode;
    this.getAjax({
      gunCode: this.carCode
    });
    this._init();
  },
  methods: {
    getAjax(a) {
      $.ajax({
        type: 'get',
        url: this.baseUrl.details,
        dataType: 'json',
        data: a,
        success: (data) => {
          if(data.code == 0) {
            this.datas = data.data
            this.siteId = this.datas.siteId;
            if(this.datas.orderVos[0]) {
              this.thirdpartyOrderId = this.datas.orderVos[0].thirdpartyOrderId;
            }
          }
        }
      })
    },
    openType(arg) {
      switch(arg) {
        case 0:
          return "";
        case 1:
          return "APP";
        case 2:
          return "APP";
        case 3:
          return "";
        case 4:
          return "";
      }
    },
    stopCharge() {
      let a = {
        orderId: this.datas.orderVos[0].thirdpartyOrderId
      }
      let temp = JSON.stringify(a);
      $.ajax({
        type: 'post',
        url: this.baseUrl.stopCharge,
        data:{
          orderId:this.datas.orderVos[0].thirdpartyOrderId
        },
        success: (data) => {
          if(data.code == 0) {
            tip("");
            setTimeout(() => {
              window.location.reload(location.href)
            }, 400)
          }
        }
      })
    },
    startCharge() {

    },
    changeLock(arg) {
      let lockNum, lockMes;
      if(arg == 1) {
        lockNum = "Lock";
        lockMes = '';
      } else {
        lockNum = "Unlock";
        lockMes = "";
      }
      $.ajax({
        type: 'post',
        url: this.baseUrl.changeLock,
        dataType: 'json',
        data: {
          id: this.datas.lockVo.lockCode,
          action: lockNum
        },
        success: (data) => {
          if(data.code == 0) {
            tip(lockMes);
            setTimeout(() => {
              window.location.reload(location.href);
            }, 400)
          }
        }
      })
    },
    changeStatus(id) {
      let temp = id;
      if(!this.chargringBol) {
        $.ajax({
          type: 'post',
          url: this.baseUrl.changeStatus,
          data: {
            regionId: this.$route.query.regionId,
            stallCode: this.$route.query.stallCode,
            status: id
          },
          success: (data) => {
            if(data.code == 0) {
              tip("");
              setTimeout(() => {
                window.location.reload(location.href);
              }, 400)
            }
          }
        })
      } else {
        tip(',');
      }
    },
    resetBol(bol) {
      this.statusBol = false;
      this.orderBol = false;
      this.gunBol = false;
      this.inductorBol = false;
      this.lockBol = false;
      this.controlBol = false;
      this.acBol = false;
      this.warnIndex = null;
      if(bol) {
        this.$set(bol, true);
      }
    },
    warnIndexs(a, b) {
      this.warnIndex = a;
      this.noteItem = b;
    },
    note() {
      this.$refs.warningNotePop.show();
      this.resetBol();
    },
    saveNote() {
      if(!this.noteItem.remark) {
        tip("!")
        return false;
      }
      $.post(apiPath.maintain.updateWarningNote, {
        recordId: this.noteItem.recordId,
        note: this.noteItem.remark
      }, (rtn) => {
        if(rtn.code === 0) {
          tip("");
        } else {
          tip("");
        }
      });
      this.$refs.warningNotePop.hide();
      this.resetBol();
    },
    stopWarn() {
      this.resetBol();
      this.$refs.warningOverPop.show();
      this.overWarn();
    },
    sendNotice(index) {
      this.noteItem = this.noticeList[index];
      $.get(apiPath.maintain.warning.notice, {
        recordId: this.noteItem.recordId,
        userId: this.noteItem.userId
      }, (rtn) => {
        if(rtn.code === 0) {
          this.warningNotice();
          tip("");
        } else {
          tip("");
        }
      });
    },
    warningNotice() {
      this.resetBol();
      ajaxHttp(apiPath.maintain.warning.noticeDetail, {
        data: {
          recordId: this.noteItem.recordId
        }
      }).then((rtn) => {
        this.noticeList = rtn.data;
        this.$refs.warningNoticePop.show();
      }).catch(() => {})
    },
    overWarn() {
      if(!this.noteItem.remark) {
        tip("!")
        return false;
      }
      $.post(apiPath.maintain.updateEndWarning, {
        recordId: this.noteItem.recordId,
        note: this.noteItem.remark,
        code: this.noteItem.code
      }, (rtn) => {
        if(rtn.code === 0) {
          tip("");
        } else {
          tip("");
        }
      });
      this.$refs.warningOverPop.hide();
    },
    orderStatus(a) {
      switch(a) {
        case -500:
          return ""
        case -40:
          return ""
        case -35:
          return ""
        case -30:
          return ""
        case -29:
          return ""
        case -20:
          return ""
        case -10:
          return ""
        case -7:
          return ""
        case -5:
          return ""
        case 0:
          return ""
        case 100:
          return ""
        case 200:
          this.chargringBol = true;
          return ""
        case 250:
          return ","
        case 300:
          return ""
        case 800:
          return ""
        case 1000:
          return ""
        case 2000:
          return ""
        default:
          break;
      }
    },
    gunStatus() {
      let temp = this.datas.chargerDataVo.status;
      this.gunstatus = temp;
      if(temp > 12) {
        return `<span style="color:#9AC88E"></span>`
      } else if(temp == -300 || temp == -200) {
        return `<span style="color:#ff0000"></span>`
      } else if(temp == 10) {
        return `<span style="color:#848484"></span>`
      } else if(temp == 0) {
        return `<span style="color:#ff0000"></span>`
      }
    },
    sensorVosStatus(a) {
      if(a == 70) {
        return `<span class="takeUp">()</span>`
      } else if(a == 10) {
        return `<span class="free"></span>`
      } else if(a == 0) {
        return `<span class="offline"></span>`
      } else if(a == -200) {
        return `<span class="offline"></span>`
      }
    },
    lockStatus() {
      let a = this.datas.lockVo.status;
      if(a == 10) {
        return `<span class="takeUp"></span>`
      } else if(a == 70) {
        return `<span class="free"></span>`
      } else if(a == 0) {
        return `<span class="offline"></span>`
      } else if(a == -200) {
        return `<span class="offline"></span>`
      }
    },
    sensorVosType(a) {
      switch(a) {
        case 1:
          return ''
        case 2:
          return ''
        case 3:
          return ''
      }
    },
    boxVoStatus() {
      switch(this.datas.boxVo.status) {
        case 100:
          return `<span style="color:#848484"></span>`
        case 0:
          return `<span style="color:#ff0000"></span>`
        case -200:
          return `<span style="color:#ff0000"></span>`
        default:
          break;
      }
    },
    _init() {
//    let bread = JSON.parse(window.localStorage.getItem('bread'));

      let arr = [{
        name: `${this.$route.query.head}`,
        router: `${this.handlerUrl(this.$route.path.split('?')[0],'/carcontrol/cardetails')}?id=${this.$route.query.id}&head=${this.$route.query.head}`
      }, {
        name:'',
        router: `${this.handlerUrl(this.$route.path.split('?')[0],'/cardetails')}?id=${this.$route.query.id}&head=${this.$route.query.head}`
      },
      {
        name: ''
      }];
      COMMON.componentHub.breadCrumb = arr;
//    this.setBreadCrumb(bread.concat(arr));
    },
    handlerUrl(url,b) {
      return url.replace(b, '');
    },
    warnDetail(a) {
      switch(a) {
        case 1:
          return `warn`;
        case 2:
          return `warn1`;
        case 3:
          return `warn2`
      }
    },
    boxType(){
      if(this.datas.boxVo.type==0){
        return ""
      }else{
        return ""
      }
    }
  },
  computed: {
    currentType() {
      if(this.datas.chargerDataVo.currentType == 0) {
        return ''
      } else {
        return ""
      }
    },
    status() {
      switch(this.datas.status) {
        case 1:
          return ''
        case 2:
          return ''
        default:
          break;
      }
    }
  },
  components: {

  }
}
module.exports = CarDetails;


