require('components/common/table-data/table-data');
require('components/base/drop-menu/drop-menu');
require('components/base/input-box/input-box');
require('components/base/pop/pop');
require('components/base/icon/icon');
require('components/base/bubble/bubble');
require('components/base/checkbox/checkbox');
require('components/base/form-area/form-area');
require('components/base/btn/btn');
require('components/common/search-tool/search-tool');
const alert = require('components/base/pop/alert');
const tip = require('components/base/pop/tip');
const dateUtil = require('src/common/utils/date');
const ajaxHttp = require('appUtil/ajaxHttp');

const store = require('appVuex/store');
const { apiPath } = require('appService/api');
const { common: commonEvent } = require('components/config/event.json');

require('./warning-record.scss');
const template = require('./warning-record.tpl');

const NetController = {
  name: "warning-record",

  template,

  data() {
    return {
      thItems: ["", "", "", "", "", "",
        "", "", "", "", ""],
      countDetailThItems: ["", "", "", "",
        "", "", ""],
      url: apiPath.maintain.warningRecord,
      historyUrl: apiPath.maintain.warning.history,
      siteNameList: apiPath.maintain.siteNameList,
      deviceList: apiPath.maintain.deviceList,
      warningName: apiPath.maintain.warningName,
      StartTimeOption: [{
        value: -1,
        text: ""
      }, {
        value: 1,
        text: ""
      }, {
        value: 2,
        text: ""
      }],
      currentItem: {},
      currentParentItem: {},
      //controllerList 
      inParent: true,
      processDropItems: (items) => {
        items.unshift({
          value: -1,
          text: ""
        });

        return items;
      },
      startTimeInput: "",
      historyTableHeader: ""
    }
  },

  store,

  methods: {
    _init() {

      if (this.$route.query.deviceId || this.$route.query.stationId
          || this.$route.query.statusId) {
        this.$refs.tableData.query(this.$route.query).fetch();
      } else {
        this.$refs.tableData.query().fetch();
      }
    },

    mouseOver() {
      this.$refs.warnTip.show(arguments[0].target);
    },

    mouseLeave() {
      this.$refs.warnTip.hide();
    },

    warningCountDetail(trItem) {
      this.currentParentItem = trItem;

      this.$refs.warningCountDetailPop.show();
      this.historyTableHeader = ` - ${this.currentParentItem.detail}${this.currentParentItem.entity}:${this.currentParentItem.showId}`;
      this.$refs.historyTable.query({
        code: this.currentParentItem.code,
        deviceId: this.currentParentItem.deviceId
      }).fetch();
    },

    controllerList(evt, trItem, inParent) {
      this.$refs.controllerList.show(evt.target);

      if (inParent) {
        this.currentParentItem = trItem;
        this.inParent = true;
      } else {
        this.inParent = false;
      }

      this.currentItem = trItem;
    },

    warningNote() {
      this.$refs.warningNotePop.show();
    },

    warningOver() {
      this.$refs.warningOverPop.show();
    },

    warningNotice() {
      ajaxHttp(apiPath.maintain.warning.noticeDetail, {
        data: {
          recordId: this.currentItem.recordId
        }
      }).then((rtn) => {
        this.noticeList = rtn.data;
        this.$refs.warningNoticePop.show();
      }).catch(()=>{})
    },

    sendNotice(index) {
      this.currentNoticeItem = this.noticeList[index];

      $.get(apiPath.maintain.warning.notice, {
        recordId: this.currentNoticeItem.recordId,
        userId: this.currentNoticeItem.userId
      }, (rtn) => {
        if (rtn.code === 0) {
          this.$refs.tableData.fetch();
          this.warningNotice();
          tip("");
        } else {
          tip("");
        }
      });
    },

    saveNote() {
      var note = this.$refs.warningNoteText.val();
      var saveNoteUrl = this.inParent ?
          apiPath.maintain.warning.saveNote :
          apiPath.maintain.warning.saveNoteInHistory;

      if (!this.$refs.warningNoteText.validate()) {
        return false;
      }

      $.post(saveNoteUrl, {
        recordId: this.currentItem.recordId,
        note: note,
        code: this.currentParentItem.code
      }, (rtn) => {
        if (rtn.code === 0) {
          this.$refs.tableData.fetch();
          this.$refs.historyTable.fetch();
          tip("");
        } else {
          tip("");
        }
      });
      this.$refs.warningNotePop.hide();
    },

    overWarning() {
      var note = this.$refs.warningOverText.val();

      if (!this.$refs.warningOverText.validate()) {
        return false;
      }

      this.$refs.warningNotePop.hide();
      $.post(apiPath.maintain.updateEndWarning, {
        recordId: this.currentItem.recordId,
        note: note,
        code: this.currentParentItem.code
      }, (rtn) => {
        if (rtn.code === 0) {
          this.$refs.tableData.fetch();
          this.$refs.historyTable.fetch();
          tip("");
        } else {
          tip("");
        }
      });
      this.$refs.warningOverPop.hide();
    },

    starTimeFilter(value) {
      return {
        text:
          `${dateUtil.time(value.startTime)}
          ${dateUtil.time(value.endTime)}`
      }
    },

    warningColor(level) {
      switch (level) {
        case 1:
          return 'danger-bg';
        case 2:
          return 'orange-bg';
        case 3:
          return 'warning-bg';
        default:
          return 'danger-bg';
      }
    }
  },

  events: {
    [commonEvent.searchTool.change](opt) {
      Object.assign(opt.queryValue, {
        startTime: this.startTimeInput,
        _index: 1
      });

      this.$refs.tableData.query(opt.queryValue).fetch();
    }
  },

  watch: {
    "startTimeInput"(val, oldVal) {
      if (Object.is(val, oldVal) || val === oldVal) {
        return false;
      }

      var queryOpt = this.$refs.tableData.queryOpt;

      Object.assign(queryOpt, {
        startTime: val
      });
      this.$refs.tableData.query(queryOpt).fetch();
    }
  },

  ready() {
    this._init();
  }
}

module.exports = NetController;


