/*
 * checkbox - 
 *
 * @props theme - 
 * @props value -  checkbox 
 * @props inputName - input  name
 * @props type - input  type(radio | checkbox)
 * @props checkboxItems - 
 * @props readOnly - 
 * @props required - 
 * @props queryName - 
 * @props errorMessage - checkbox 
 *
 * @events change - checkbox
 */

const Vue = require('vue');
require('components/base/icon/icon');
require('./check-ele');

require('./checkbox.scss');
const template = require('./checkbox.tpl');

const { checkbox: checkboxEvent } = require('components/config/event.json');
const baseMixin = require('components/mixin/base');
const formMixin = require('components/mixin/form');
const arrayUtil = require('src/common/utils/array');

const TYPE_RADIO = 'radio';
const TYPE_CHECKBOX = 'checkbox';

var CheckboxGroup = Vue.extend({
  name: "Checkbox",
  mixins: [baseMixin, formMixin],
  template,
  props: {
    theme: {
      type: String,
      default: "primary"
    },
    checkboxItems: {
      type: Array,
      default: () => []
    },
    inputName: {
      type: String,
      default: ""
    },
    type: {
      type: String,
      default: TYPE_RADIO
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    queryName: {
      type: String,
      default: ""
    },
    errorMessage: {
      type: String,
      default: ""
    },
    required: {
      type: Boolean,
      default: true
    },
    value: [Number, Array]
  },
  data() {
    return {
      currentIndex: 0,
      text: "",
      verified: true,
      dangerTip: ""
    }
  },
  methods: {
    /**
     * checkboxchange
     *
     * @return {Function}
     **/
    _dispatchChange() {
      return this.$dispatch(checkboxEvent.change, {
        value: this.value,
        text: this.text,
        queryName: this.queryName,
        dispatcher: this
      });
    },

    /**
     * checkbox
     *
     * @return {Function}
     **/
    _initCheckbox() {
      this._ininCheckboxItems();

      if (this.isCheckbox) {
        this.text = [];
      }

      if (!this.value && this.value !==0 ) {
        if(this.isRadio){
          this.value = undefined;
          this.verified = false;
        } else {
          this.value = [];
        }
      }
    },

    /**
     * checkboxItems
     *
     * @return {Function, Object}
     **/
    _ininCheckboxItems() {
      var $el = $(this.$el);
      var $checkEle = $el.find('.checkbox-items-slot .check-ele');
      var items = [];
      if (arrayUtil.isEmpty(this.checkboxItems) && $checkEle.length !== 0) {
        $checkEle.each((index, el) => {
          var $el = $(el);

          items.push({
            value: parseInt($el.attr('value'), 10),
            text: $el.text()
          });
        });

        return this.$set('checkboxItems', items);
      }

      return this;
    },

    /**
     * 
     *
     * @return {Function}
     **/
    changeCheck(item, index) {
      if (this.isRadio) {
        this.value = item.value;
        return this.setCurrentIndex(this.value);
      } else {
        return this.setText();
      }
    },

    /**
     * checkboxicon
     *
     * @param { String } - checkbox
     * @return { Function, Object }
     **/
    iconName(val) {
      if (this.isRadio) {
        return this.value === val ? 'dot-circle-o' : 'circle-o';
      } else if (this.isCheckbox) {
        return this.value.includes(val) ? 'check-square' : 'square-o';
      }
    },

    /**
     * checkboxtext
     *
     * @return {Function, String}
     **/
    setText() {
      if (this.isRadio) {
        return this.checkboxItems[this.currentIndex]['text'];
      } else {
        if (!arrayUtil.isArray(this.value) ||
            arrayUtil.isEmpty(this.value)) {
          return false;
        }

        this.text = [];

        return this.value.forEach((item) => {
          this.checkboxItems.forEach((ele) => {
            if (item === ele.value) {
              this.text.push(item);
            }
          });
        });
      }
    },

    /**
     *  currentIndex
     *
     * @return {Function, Object}
     **/
    setCurrentIndex(val) {
      if (this.isRadio) {
        return this.checkboxItems.forEach((item, index) => {
          if(item === this.value)
          this.currentIndex = index;
        });
      }

      return this;
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    verify() {
      this.dangerTip = ` ${this.errorMessage}!`;

      return this.verified;
    },

    /**
     * 
     *
     * @return {Object} - this - 
     */
    validate() {
      this.verify();

      if (!this.verified) {
        tip(this.dangerTip);

        return false;
      }

      return this;
    }
  },

  computed: {
    isCheckbox() {
      return this.type === TYPE_CHECKBOX;
    },

    isRadio() {
      return this.type === TYPE_RADIO;
    }
  },

  watch: {
    'value'(val, oldVal) {
      if(this.isRadio){
        this.verified = true;
      }

      if (Object.is(val, oldVal) || val === oldVal) {
        return false;
      }

      this._dispatchChange();
    }
  },

  compiled() {
    this._ininCheckboxItems();
    this._initCheckbox();
    this._dispatchChange();
  }
});

module.exports = Vue.component('checkbox', CheckboxGroup);


